import styled from 'styled-components';

export const Container = styled.div`
  .footer {
    display: flex;
    justify-content: center;
  }

  .social {
    display: flex;
    flex-direction: row;

    position: fixed;
    bottom: 5rem;
  }

  .social2 {
    list-style-type: none;
    font-weight: bold;
    font-size: 3rem;
    margin-right: 2rem;
  }

  .social3 {
    background: none;
    transition: 0.7s;
  }

  .social3:hover {
    color: #7bf021;
  }
`;
