import React from 'react';

import { Container } from './styles';
import {
  FaFacebookSquare,
  FaTwitterSquare,
  FaGithubSquare,
  FaLinkedin,
} from 'react-icons/fa';
function Descricao() {
  return (
    <Container>
      <div className="footer">
        <ul className="social">
          <li className="social2">
            <a className="social3" href="http://">
              Facebook <FaFacebookSquare />
            </a>
          </li>
          <li className="social2">
            <a className="social3" href="http://">
              Twitter <FaTwitterSquare />
            </a>
          </li>
          <li className="social2">
            <a className="social3" href="http://">
              Github <FaGithubSquare />
            </a>
          </li>
          <li className="social2">
            <a className="social3" href="mailto:">
              Linkedin <FaLinkedin />
            </a>
          </li>
        </ul>
      </div>
    </Container>
  );
}

export default Descricao;
